// Contains all the CSS that handles the layout, such as the container and any grid systems
body {
  @include main-font;
  background-color: $mainColor;
  margin: 0px;
}

main {
  p {
    margin: 25px 10%;
    line-height: 170%;
    text-align: justify;
    @include bp(tablet) {
      margin: 25px 30px;
    }
    @include bp(phone) {
      margin: 25px 20px;
      font-size: 16px;
    }
  }
}
